.main-header{
    z-index: 10;
    box-shadow:0px 1px  rgba(0,0,0,0.15);
    transition: top .3s;
    margin:0 auto;
    font-family:Thasadith-Regular;
    .link{
        margin-left: 30px;
        cursor: pointer;
        &:active,
        &:focus{
            color: #ff385c; 
        }
        &:hover{
            color: #ff385c;
        }
       
        .active{
            color: #ff385c;  
        }
    }
    &:not(:last-child){
        margin-right: 50px;
    }

    .logo{
        margin-left: 50px;
        font-family:Thasadith-Regular;
        font-size: rem(20px);
        font-weight: 300;
    }
    
    a{
        color:#333233;

        &:hover{
            color: #ff385c;
            transition: color 0.8s;
        }
    }
    .active{
        color: #ff385c;  
    }
    
}
@media (max-width: 750px){
    .main-header{
        font-size: rem(12px);
        .logo{
            font-size: rem(12px); 
            margin-left: rem(10px); 
        } 
        .link{
            margin-left: 18px;
        }
        &:not(:last-child){
            margin-right: 10px;
        }
    } 
    .main-nav{
        padding: 0px;
    }
   
}