.proj{
   h2,h3{
    font-family:Thasadith-Regular;
   } 
   .proj-title{
      margin: 80px 0px 50px;
   }

 
   .proj-gif{
     
     max-width: 100%;
     display: block;
     transition: opacity ease-out 250ms;
     background-color: rgba(128, 128, 128,0.5);
     opacity: 1;
   }
   .proj-gif:hover{
 background-color: rgba(128, 128, 128,0.5); 
 color: #ff385c;
      // opacity: 0.5;
   }
  
   a{
      color: #ff385c;
   }
   .my-proj{
      display: grid;
      grid-template-columns: repeat(3,350px);
      gap: 10px;
     
   }
   .make-box, .appsus-box{
      border: 1px solid rgb(221, 221, 221);
        border-radius: 12px;
        padding: 24px;
        box-shadow: rgba(0, 0, 0, 0.12) 0px 6px 16px;

   }

   .meme-box{
      border: 1px solid rgb(221, 221, 221);
      border-radius: 12px;
      padding: 24px;
      box-shadow: rgba(0, 0, 0, 0.12) 0px 6px 16px;
      p{
         margin-top: 50px;
      }
    
   }

  
}

@media (max-width: 750px){
   .proj{
      h2{
         font-size:rem(17px); 
        } 
      .proj-title{
         font-size:rem(17px);
         margin: 50px 0px 50px;
      }
      .my-proj{
         display: flex;
        flex-direction: column;
        
      }
      .meme-box{
        
         p{
            margin-top: 20px;
         }
       
      }
      .make-box, .appsus-box, .meme-box{
         font-size: rem(15px);
      }
   }

}