.footer{
    font-family:Thasadith-Regular;
    margin-top: 50px;

    .first-section{
     background:#f7f7f7;
     height: 150px;
     color:#0e0d0d;
   
    }
    .second-section{
        background:#2c2b2b;
        height: 60px;  
        color: #f7f7f7; 
      font-size: rem(13px);
        
    }
    .socials{
      color:#0e0d0d;
     .MuiSvgIcon-root{
      color:#ff385c;
      width: 30px;
      height: 30px;
      &:hover{
        color:rgba(255,56,92, 0.80);
        transition: color 0.5s;
        
              }
     }   
    }
    .cv-btn{
      width: 80px;
      height: 30px;
      outline: none;
      border: none;
      background-color:rgb(255, 56, 92) ;
      color: #f7f7f7;
      font-family:Thasadith-Bold;
      border-radius: 7px;
      cursor: pointer;

      &:hover{
background-color:rgba(255,56,92, 0.80);
transition: background-color 0.5s;

      }
    }
    

}
@media (max-width: 750px){
  .footer{
    .first-section{
      font-size: rem(12px);
    }
    .cv-btn{
      width: 70px;
      height: 20px;
      font-size: rem(12px);
      margin-top: 5px;
    }
    .second-section{
      font-size:rem(12px) ;
    }
}
}