.hero{
  background:#f7f7f7;
  height: 500px;
  margin: 0px;
  justify-content: center;
  color:#0e0d0d;
   
  .profile-pic{
  border-radius: 50%;
  height: 50%;
  width: 50%;
  }
  h3{
      font-size: rem(30px);
      font-family:Thasadith-Bold;
      color:#0e0d0d;
   
  }
  .stars{
  span{
      margin: 0px 20px;
      color: $clr1;
    }
  }
 

    
}
@media (max-width: 750px){
  .hero{
    height: 600px;
    h3{
      font-size: rem(20px);
    }
    .stars{
      font-size: rem(13px);
     justify-content: center;
     margin-left: 30px;
   
      span{
        margin: 0px 3px;
       
      }
      .MuiSvgIcon-root{
        font-size: rem(10px);
      }
    }
  
  
  }
  .portfolio-card{
    margin-bottom: 100px;
  }
 
}



